import React from 'react';
import '../index.css';
import FaArrowUp from 'react-icons/lib/fa/arrow-up';
import FaArrowDown from 'react-icons/lib/fa/arrow-down';
import FaArrowLeft from 'react-icons/lib/fa/arrow-left';
import FaArrowRight from 'react-icons/lib/fa/arrow-right';
// import * as FontAwesome from 'react-icons/lib/fa';


class Game extends React.Component {
    constructor(props){
        super(props)
        this.state = {
            gameStatus: 'new',
            remainingSeconds: this.props.initialSeconds,
            score :  0,
            level: 0,
            target: 0,
        };
    }

    gettarget = () => {
        this.setState(
          () => {
            const newTarget = Math.floor(Math.random() * (3 - 0 + 1)) + 0;
            console.log("newTarget:", newTarget);
            return {target:  newTarget};
          }, function(){
              this.changetarget();
          });
    }

    changetarget = () =>{
      for (let index = 0;index < 4;index++)
        document.getElementById("arrow" + index).style.color = 'black';
      var target = document.getElementById("arrow" + this.state.target);
      console.log("changed!");
      target.style.color = 'red';
    }

    cleartarget = () =>{
      for (let index = 0;index < 4;index++)
      document.getElementById("arrow" + index).style.color = 'black';
    }

    startGame = (props) => {
      document.getElementById("gameover").innerHTML = '';
      if (this.state.remainingSeconds === 0)
      this.state.remainingSeconds = this.props.initialSeconds;
      this.state.score = 0;
      this.setState({ gameStatus: 'playing' }, () => {
        this.changetarget();
        this.intervalId = setInterval(() => {
          this.setState((prevState) => {
            const newRemainingSeconds = prevState.remainingSeconds - 1;
            if (newRemainingSeconds === 0) {
              clearInterval(this.intervalId);
              this.cleartarget();
              document.getElementById("gameover").innerHTML = 'GAME OVER';
              this.props.setScore(this.state.score);
              return { gameStatus: 'lost', remainingSeconds:  0};
            }
            return { remainingSeconds: newRemainingSeconds };
          });
        }, 1000);
      });
    };
      

    selectNumber = (numberIndex) => {
      console.log("clicked!", numberIndex);
      if (this.state.gameStatus !== 'playing') {
        return;
      }
      if (numberIndex === this.state.target)
      {
        console.log("good!");
        this.gettarget();
        // this.changetarget();
        this.setState(()=>{
          console.log(this.state.score + 10);
          return { score: this.state.score + 10 };
        })
      }
      this.setState(
        () => {
          if (this.state.gameStatus !== 'playing') {
            clearInterval(this.intervalId);
          }
        }
      );
    };
    

    render() {
      const { gameStatus, remainingSeconds } = this.state;
      let start;
      let gameover = <div id="gameover" className="timer-value"></div>;
      if (this.state.gameStatus !== 'playing')
      {
        start =  <button onClick={this.startGame} >Start</button>;
      }
      else{
        start = <button onClick={this.props.onPlayAgain} >Restart</button>;
      }
      return (
        <div className="game">
          <div >
            You surely know how it works :)
          </div>
          <div className="timer-value">Timer:{remainingSeconds}</div>
          {gameover}
          <div className="challenge-numbers">
            <div className="number" id="arrow0" 
        onClick={() => this.selectNumber(0)}><FaArrowUp/></div>
            <div className="number-middle" id="arrow1" 
        onClick={() => this.selectNumber(1)}><FaArrowLeft/></div>
            <div className="number-middle" id="arrow2" 
        onClick={() => this.selectNumber(2)}><FaArrowRight/></div>
            <div className="number" id="arrow3" 
        onClick={() => this.selectNumber(3)}><FaArrowDown/></div>
          </div>
          <div className="score-value">Score:{this.state.score}</div>
            {start}
        </div>
      );
    }
  }
    export default Game;


