import React from 'react';
import { Button } from 'react-bootstrap';

// import { BrowserRouter as Router, Route, Link } from "react-router-dom";

import LeaderBoard from './LeaderBoard';
import Game from './Game';
// import Play from './Play';
// import Itemlist from './ItemList';

class App extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            gameId: 1,
            inserted: 0,
            Username: null,
            UserScore: null,
            screen_type: 1,
            Scores: [],
            isLoggedIn: false,
            insert_name: '',
            insert_value: '',
        };
        // this.handleLogoutClick = this.handleLogoutClick.bind(this);
        this.handleClickplay = this.handleClickplay.bind(this);
        this.handleClickLeaderBoard = this.handleClickLeaderBoard.bind(this);

    }
    handleClickplay() {
        this.setState({screen_type: 1});
    }
    handleClickLeaderBoard() {
        this.setState({screen_type: 2});
    }
    
                
    resetGame = () =>
    {
        this.setState((prevState) => ({
        gameId: prevState.gameId + 1,
        inserted: 0
        }));
    }
     

    AddScores(newscore)
    {
        console.log('AddScore', newscore);
        let oldScores = this.state.Scores;
        newscore.id = this.state.Scores.length + 1;
        let newArticles = [...oldScores, newscore];
        this.setState({ Scores: newArticles});
        console.log('NewScores', newArticles);
    }

    handleSubmit = (event) => {
        event.preventDefault();
        const newscore = { name: this.state.insert_name, score: this.state.insert_value };
        if (newscore.name !== '' && newscore.score !== '' && this.state.inserted === 0)
            this.AddScores(newscore);
        else if (this.state.inserted === 0)
        {
            alert("Rejouez pour resubmit!");
            return ;
        }
        this.setState({ insert_name: '', insert_value: '', inserted: 1});
    }

    setScore = (score) => {
        console.log("new score", score);
        this.setState({
            insert_value: score,
            inserted: 0,
        })
    }

    render() {

        const btnmenu = {
            width: '50%',
            display:
            'inline-flex',
            alignItems: 'centers',
            justifyContent: 'center'
        }

        let show_state = this.state.screen_type;
        let title = 'Click it!';
        let screen;
        if (show_state === 1){
            screen =  //<h3>Insert game</h3>
            <Game         initialSeconds={5} setScore ={this.setScore}
        onPlayAgain={this.resetGame}         key={this.state.gameId}
        />;

        }
        else
            screen = <LeaderBoard score={this.state.Scores} />;

        let addtoleaderboard;
        if (this.state.insert_value !== '' && this.state.screen_type === 1 && this.state.inserted === 0)
        {
            addtoleaderboard =                 <center>
            <form onSubmit={this.handleSubmit}>
                <input type="text"
                        placeholder="name"
                        maxlength="12"
                        value={this.state.insert_name}
                        onChange={(event) => this.setState({ insert_name: event.target.value })}
                />
                {/* <input type="number"
                        className="quantity"
                        placeholder="Score"
                        min="0"
                        value={this.state.insert_value}
                        onChange={(event) => this.setState({ insert_value: event.target.value })}
                /> */}
                <button type="submit" className="btn btn-success">add</button>
            </form>
            </center>
        }

        return (
            <div>
                <h1>
                   {title}
                </h1>
                <ul>
                </ul>
                    <div role="group" aria-label="...">
                        <div className="btn-group" role="group" style={btnmenu}>
                            <Button style={{width: '90%'}} onClick={this.handleClickplay}>Play Game</Button>
                        </div>
                        <div className="btn-group" role="group" style={btnmenu}>
                            <Button style={{width: '90%'}} onClick={this.handleClickLeaderBoard}>Leader Board</Button>
                        </div>
                    </div>
                <hr />
                {/* <h2>{show_state}</h2> */}
                {screen}
                {addtoleaderboard}
            </div>
        );
    }
}

export default App;


