import React from 'react';

class LeaderBoard extends React.Component {

  constructor(props) {
    super(props);
  }

  render() {
    let title_centerize = {
      fontSize: '30px',
      width:'30%',
      textAlign: 'center',
      float:'left'
    };
    let centerize = {
      width:'30%',
      textAlign: 'center',
      float:'left'
    };
    let title_number = {
      marginLeft: '20%',
      fontSize: '30px',
      width:'10%',
      textAlign: 'center',
      float:'left'
    }
    let number = {
      marginLeft: '20%',
      // fontSize: '30px',
      width:'10%',
      textAlign: 'center',
      float:'left'
    }
    return (
      <div>
        <h2 style ={{textAlign: 'center'}}>Leader Board</h2>
          <div style={title_number}>Rank</div>
          <div style={title_centerize}>Player</div>
          <div style={title_centerize}>Score</div>
        {
          this.props.score
          .sort((a, b) => parseInt(a.score) < parseInt(b.score))
          .map((score, i) => <div key={score.id}>
          <div style={number}> {i + 1} </div>
            <div style={centerize}> {score.name} </div>
            <div style={centerize}> {score.score} </div>          
              </div>)        
        }
      </div>
    );
  }
}
export default LeaderBoard;


